.array {
  margin: 0.75%;
}
.array-container {
  display: flex;
  justify-content: center;
}
.sort-button-container {
  display: flex;
  justify-content: center;
}
.array-bar {
  display: inline-block;
  margin: 0 2px;
  background-color: #3dcbe0;
}
.transparent-array-bar {
  display: inline-block;
}
.button-container {
  display: flex;
  justify-content: center;
  margin: 1%;
}
.radio-label {
  margin-left: 1%;
  margin-right: 1%;
}
.slider-container {
  display: flex;
  justify-content: center;
  margin: 1%;
}
.slider-label {
  display: flex;
  justify-content: center;
  margin: 1%;
}
.slider {
  margin-right: 10%;
}
